// export default Icon;
import React from 'react';

const Icon = ({
                  type = 'default',
                  gameType = null, // Para iconos espec√≠ficos del juego
                  size = 'medium',
                  className = '',
                  ...props
              }) => {
    // Iconos del juego
    const gameIcons = {
        question: "data:image/svg+xml;base64,",
        answer: "data:image/svg+xml;base64,"
    };

    const sizes = {
        small: 'w-4 h-4',
        medium: 'w-6 h-6',
        large: 'w-8 h-8',
        game: 'w-8 h-8 mx-auto mb-4'
    };

    if (gameType) {
        return (
            <img
                className={`icon-card__answer ${sizes.game} ${className}`}
                src={gameIcons[gameType]}
                alt={gameType === 'question' ? 'Pregunta' : 'Respuesta'}
                {...props}
            />
        );
    }

    // Para iconos est√°ndar, podr√≠as usar SVG inline o una librer√≠a como Heroicons
    return (
        <div className={`${sizes[size]} ${className}`} {...props}>
            {/* Aqu√≠ ir√≠an tus iconos est√°ndar */}
        </div>
    );
};

export default Icon;
